<template>
  <div class="info">
    <div class="user-info">

      <div class="user-item"  @click="myOrdersClick">
        <img src="~assets/img/pro/order.svg" alt="">
        <div class="info-text">我的订单</div>
      </div>

      <div class="user-item"  @click="selectClick">
        <img src="~assets/img/pro/select.svg" alt="">
        <div class="info-text">我的收藏</div>
    `</div>
    </div>
    <!--管理员页面-->
    <div class="guanli-info" v-if="isAdminTrue" >
      <div class="guanli-item"  @click="ordersClick">
        <img src="~assets/img/pro/order.svg" alt="">
        <div class="info-text">订单管理</div>
      </div>
      <div class="guanli-item"  @click="goodsClick">
        <img src="~assets/img/pro/shezhi.svg" alt="">
        <div class="info-text">商品管理</div>
      </div>
      <div class="guanli-item" @click="moneyClick">
        <img src="~assets/img/pro/m.svg" alt="">
        <div class="info-text">查看收入</div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'UserInfo',
    props: {
      userInfo: {
        type: Object,
        default() {
          return {}
        }
      }
    },
    computed: {
      isAdminTrue() {
        if(this.userInfo.idadmin == 'false') {
          return false
        }else {
          return true
        }
      }
    },
    methods: {
      //我的订单
      myOrdersClick() {
         if(this.userInfo.uid == 0){
          this.$toast.show("请先登录")
        }else {
           this.$router.push('/order')
        }
      },
      //我的收藏
      selectClick() {
         if(this.userInfo.uid == 0){
          this.$toast.show("请先登录")
        }else {
          this.$router.push('/collect')
        }
      },
      //订单管理
      ordersClick() {
        this.$router.push('/allorders')
      },
      //商品管理
      goodsClick() {
        this.$router.push('/manager')
        
      },
      //收入
      moneyClick() {
         this.$router.push('/income')

      }
    }
  }
</script>

<style scoped>

  .user-info{
    background-color: #fff;
    border-bottom: 20px solid #f2f5f8;
  }
  .user-item, .guanli-item{
    height: 44px;
    line-height: 44px;
  }
  img{
    width: 18px;
    height: 18px;
    float: left;
    margin-top: 13px;
    margin-left: 25px;
  }
  .guanli-info{
    background-color: #fff;
  }
  .info-text{
    font-size: 14px;
    margin-left: 55px;
  }
</style>