<template>
  <div class="user-login">
    <div class="user-top">
      <div  class="user-left">
        <img class="user-img" :src="userImg" alt="">
      </div>
      <div class="user-right">
        <div v-if="idUserLogin">{{userName}}</div>
        <div v-else @click="loginRegist">登录/注册</div>      
      </div>
    </div>
    <div class="user-bottom" v-if="idUserLogin">
      <div class="bot-left" @click="downloadImg">上传头像</div>
      <div class="bot-right" @click="changeUser">修改密码</div>
      <div class="bot-rights" @click="exitUser">退出登录</div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'UserLogin',
    props: {
      //从父组件获取  用户名   用户Id    用户的图片
      userName: {
        type: String,
        default: null
      },
      userId: {
        type: Number,
        default: 0
      },
      userImg: {
        type: String,
        default: require('assets/img/pro/me.svg')
      }
      },
      computed: {
        //用户是否登录
        idUserLogin() {//不为0时返回true
          return this.userId !== 0
        }
      },
      methods: {
        //登录注册
        loginRegist() {
          //跳转到登录注册页面
          this.$router.push('/login')
        },
        //退出登录
        exitUser() {
          //将store中的用户id删除
          let user = {
            uid:0,
            idadmin: 'false'
          };
          this.$store.state.userInfo = user;

        },
        //上传图片
        downloadImg() {
          //跳转到上传图片页面
          this.$router.push('/uploadimg')
        },
        //跳转到修改密码页面
        changeUser() {
          this.$router.push('/changepassword')
        }
    }
  }
</script>

<style scoped>
  .user-login{
    background-color: #ff8198;
    padding: 15px;
    height: 130px;
    border-bottom: 20px solid #f2f5f8;
  }
  .user-img{
    width: 70px;
    height: 70px;
    background-color: #fff;
    border-radius: 40px;
    float: left;
  }
  .user-right{
    color: white;
    margin-top: 20px;
    line-height: 60px;
    margin-left: 100px;
    font-size: 18px;
    font-weight: 300;
  }
  .user-bottom{
    margin-top: 40px;
    font-size: 12px;
    color: #fff;
    clear: both;
    display: flex;
    justify-content: space-between;
  }
</style>